import React from "react";
import { Link } from "react-router-dom";

const Home = () => {
    return (
        <section id="section__home" className="section__home">
            <div className="container home__body">
                <div className="row">
                    <div className="col-sm-12">
                        <div className="home__content">
                            <h1 className="home__heading">.Net</h1>

                            <ol className="breadcrumb">
                                <li><Link to="index.html">Главная</Link></li>
                                <li className="active">Курс по .NET</li>
                            </ol>

                        </div>
                    </div>
                </div>
            </div>
            <div className="home__bg"></div>
        </section>
    )
};

export default Home;