import React from 'react';
import './courses.css';
import Course from "../course";
import courses from "./store";

const CoursesSection = () => {
    return (
        <section className="section__courses" id="section__courses">
            <div className="container">
                <div className="row">
                    <div className="col-sm-12">
                        <div className="section__subtitle light__subtitle">
                            Доступные <span>курсы</span>
                        </div>
                        <h2 className="section__title light__title">
                            Наши курсы
                        </h2>
                    </div>
                </div>
                <div className="row courses-list">
                    { courses.map( course => <Course key={course.id} course={course}/>)}
                    { courses.length > 4 ? (                    <div className="col-sm-12">
                            <a href="courses.html" className="btn btn-primary">All courses</a>
                        </div>
                    ) : null}
                </div>
            </div>
        </section>
    );
};

export default CoursesSection;