import React from 'react';
import './navbar-item.css';
import NavbarSubmenu from "../navbar-submenu";


const NavbarItem = (props) => {
    const { name, link, submenuItems = [] } = props.item;
    return (
        submenuItems.length === 0 ?
        <a href={link}>{name} </a> :
            <React.Fragment><a href={link}  className="dropdown-toggle" data-toggle="dropdown" role="button" aria-haspopup="true" aria-expanded="false">{name} <i
                className="ion-android-arrow-down"></i></a>
                <NavbarSubmenu submenu={submenuItems} /></React.Fragment>
    );
};

export default NavbarItem
