import React from 'react';
import './about.css';

const AboutSection = () => {
    return(
        <section className="section__about" id="section__about">
            <div className="container">
                <div className="row">
                    <div className="col-sm-12">
                        <div className="about__body">
                            <div className="about__icon">
                                <i className="ion-ios-bookmarks-outline"></i>
                            </div>
                            <h2 className="section__title dark__title">
                                О нас
                            </h2>
                            <p className="about__desc">
                                IT компания "СоларЛаб" явлется крупнейшей в Севастополе. Мы проводим курсы с 2016 года. Выпускниками курсов стали десятки разработчиков, многие из них прошли путь от стажеров до специалистов уровня Senior, и уже более трех лет успешно работают в нашей компании.
                            </p>
                            <div className="about__quote">
                                <h2 className="about_quote__text"><i className="ion-quote"></i> Чему бы ты не учился, ты учишься для себя <span
                                        className="quote_end"><i className="ion-quote"></i></span></h2>
                                <p className="about_quote__author">
                                    <span>Петроний Арбитр Гай</span>
                                </p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default AboutSection;