import React from "react";
import './registerForm.css'
import emailjs from 'emailjs-com';
import classnames from 'classnames';
import Snackbar from "@material-ui/core/Snackbar";
import MuiAlert from "@material-ui/lab/Alert";

function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}

export default class RegisterForm extends React.Component {

    REQUIRED_FILED_ERROR = 'Поле обязательно к заполнению';
    state = {
        name: '',
        email: '',
        course: '',
        message: '',
        errors: {},
        disableButton: false,
        openSnackbar: false,
        snackbarType: 'success'
    };

    errors = {};
    onSubmitClickHandler = (e) => {
        e.preventDefault();
        this.validateForm();
        this.setState({errors: this.errors}, () => {
            if (Object.keys(this.state.errors).length > 0) {
                console.log(this.state.errors);
                return;
            }
            this.sendEmail();
        })
    };

    sendEmail = () => {
        const templateParams = {
            from_name: this.state.name,
            email: this.state.email,
            message_html: this.state.message,
            course: this.state.course
        };
        this.setState({disableButton: true});
        emailjs.send('outlook', 'template_lRe8rgRx', templateParams, 'user_mJzdeQTkihv186xrhhoD2')
            .then((result) => {
                this.setState({
                    name: '',
                    email: '',
                    course: '',
                    message: ''
                });
                this.setState({disableButton: false, snackbarType: 'success', openSnackbar: true});
            }, (error) => {
                this.setState({disableButton: false, snackbarType: 'error', openSnackbar: true});
            });

    };

    onChangeHandler = (e) => {
        this.setState({[e.target.name]: e.target.value});
        if (e.target.value) {
            const errors = this.state.errors;
            delete errors[e.target.name];
            this.setState({errors: errors});

        } else {
            this.setState({errors: {[e.target.name]: this.REQUIRED_FILED_ERROR}})
        }
    };

    validateForm = () => {
        this.errors = this.state.errors;
        if (this.state.name === '') {
            this.errors['name'] = this.REQUIRED_FILED_ERROR
        }
        if (this.state.email === '') {
            this.errors['email'] = this.REQUIRED_FILED_ERROR
        }
        if (this.state.message === '') {
            this.errors['message'] = this.REQUIRED_FILED_ERROR
        }
        if (this.state.course === '') {
            this.errors['course'] = this.REQUIRED_FILED_ERROR
        }
    };

    handleClose = () => {
        this.setState({openSnackbar: false});
    };

    render() {
        return (
            <form id="form_sendemail" onSubmit={this.onSubmitClickHandler}>

                <div className="form-group">
                    <label htmlFor="email" className="sr-only">ФИО*</label>
                    <input type="text" name="name" id="Фио"
                           placeholder="Укажите ФИО"
                           value={this.state.name}
                           className={classnames('form-control', {'is-invalid': this.state.errors.name})}
                           onChange={this.onChangeHandler}
                           disabled={this.state.disableButton}
                           onBlur={this.onChangeHandler}/>
                    <div className="invalid-feedback">{this.state.errors.name}</div>
                </div>

                <div className="form-group">
                    <label htmlFor="email" className="sr-only">Email*</label>
                    <input type="email" name="email"
                           className={classnames('form-control', {'is-invalid': this.state.errors.email})}
                           id="email"
                           placeholder="Укажите свой email адрес"
                           value={this.state.email}
                           onChange={this.onChangeHandler}
                           disabled={this.state.disableButton}
                           onBlur={this.onChangeHandler}/>
                    <div className="invalid-feedback">{this.state.errors.email}</div>
                </div>

                <div className="form-group">
                    <label htmlFor="name" className="sr-only">Курс*</label>
                    <input type="text" name="course"
                           className={classnames('form-control', {'is-invalid': this.state.errors.course})}
                           id="course"
                           placeholder="Укажите назание курса"
                           value={this.state.course}
                           onChange={this.onChangeHandler}
                           disabled={this.state.disableButton}
                           onBlur={this.onChangeHandler}/>
                    <div className="invalid-feedback">{this.state.errors.course}</div>
                </div>

                <div className="form-group">
                    <label htmlFor="message" className="sr-only">Сообщение</label>
                    <textarea name="message"
                              className={classnames('form-control', {'is-invalid': this.state.errors.message})}
                              id="message" rows="6"
                              placeholder="Расскажите нам о себе"
                              value={this.state.message}
                              onChange={this.onChangeHandler}
                              disabled={this.state.disableButton}
                              onBlur={this.onChangeHandler}></textarea>
                    <div className="invalid-feedback">{this.state.errors.message}</div>
                </div>

                <div className="form-group">
                    <small className="text-muted">* Все поля обязательны.</small>
                </div>

                <button type="submit" disabled={this.state.disableButton} className="btn btn-block btn-accent button-submit">
                    { this.state.disableButton ? <i className='preloader'></i> : 'Отправить сообщение' }
                </button>
                <Snackbar open={this.state.openSnackbar} autoHideDuration={6000} onClose={this.handleClose}>
                    <Alert onClose={this.handleClose} severity={this.state.snackbarType}>
                        {this.state.snackbarType === 'success' ? 'Ваша заявка успешно отправлена' : 'Во время отправки произошла ошибка. Попробуйте позже или свяжитесь с нами другим способом'}
                    </Alert>
                </Snackbar>
            </form>)
    }

}