import React from "react";
import './contacts.css';
import RegisterForm from "../registerForm";

const ContactsSection = () => {
    return(
        <section className="section__contact" id="section__contact">
            <div className="container">
                <div className="row">
                    <div className="col-sm-12">
                        <div className="section_title__body">
                            <div className="section__subtitle dark__subtitle">
                                Контакты
                            </div>
                            <h2 className="section__title dark__title">
                            </h2>
                            <p className="section_title__desc">
                            </p>
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="col-sm-6">
                        <h3>Регистрация на курсы</h3>
                        <div className="alert" id="form_message" role="alert"></div>
                        <RegisterForm />
                    </div>
                    <div className="col-sm-6">
                        <div className="contact_info__body">
                            <h3>Остались вопросы?</h3>
                            <div className="contact_info__item">
                                <div className="contact__title">
                                    Email<span>.</span>

                                </div>
                                <div className="contact__info">
                                    <div className="contact_info__wrapper">
                                        <a href="mailto:education@solarl.ru">education@solarl.ru</a>
                                    </div>
                                </div>
                            </div>
                            <div className="contact_info__item">
                                <div className="contact__title">
                                    VK<span>.</span>
                                </div>
                                <div className="contact__info">
                                    <div className="contact_info__wrapper row">
                                        <div className="col-md-6" style={{textAlign: 'center'}}>
                                            <a href="https://vk.com/tanya_pichugova" target="_blank" rel="noopener noreferrer"><img style={{borderRadius: '50%'}} width="100" src="/assets/img/tanya.jpg" alt="Татьяна" /> Татьяна</a>
                                        </div>
                                        <div className="col-md-6" style={{textAlign: 'center'}}>
                                            <a href="https://vk.com/lichi_alice" target="_blank" rel="noopener noreferrer"><img style={{borderRadius: '50%'}}  width="100" src="/assets/img/alice.jpg" alt="Алиса" /> Алиса</a>
                                        </div>


                                    </div>
                                </div>
                            </div>
                            <div className="contact_info__item">
                                <div className="contact__title">
                                    Адрес<span>.</span>
                                </div>
                                <div className="contact__info">
                                    <div className="contact_info__wrapper">
                                        <p><a rel="noopener noreferrer" href='https://yandex.ru/maps/-/CKAxY2yL' target='_blank'> Севастополь, ул.Большая Морская, 23, 3й этаж</a></p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default ContactsSection;