import React from "react";
import Home from "./sections/home";
import course from './store';

const CoursePage = () => {
    console.log(course);
    return (
        <div id='course-single__page'>
            <Home/>
            <section className="section__course-single">
            <div className="container">
                <div className="row">
                    <div className="col-sm-10 col-md-12">
                        <div className="course__body">
                            <div className="course__title">
                                С# .NET Core
                            </div>
                            <div className="course__img">
                                <img src="\assets\img\blog-item_img-2.jpg" className="img-responsive" alt="..." />
                            </div>
                            <h3 className="course_desc__title">Описание курса</h3>
                            <p className="course__desc">Курс нацелен на слушателей имеющих базовые знания языка С#, понимание основ ООП
                                и реляционных баз данных. На протяжении 2,5 месяцев вы будете разрабатывать в командах собственные проекты под руководством преподавателей и наставников (действующих Senior разработчиков нашей компании).
                                Основная задача курсов - научить слушателей не только технологиям, но и реальной проектной работе.
                                Цель курсов: дать необходимый багаж знаний, который поможет вам устроиться на работу в SolarLab в качестве стажеров.</p>
                            <h3 className="course_desc__title">Наши требования</h3>
                            <ul className="course_requirements__list">
                                <li><i className="ion-android-arrow-forward"></i> Базовые знания языка C#</li>
                                <li><i className="ion-android-arrow-forward"></i> Понимание основ ООП</li>
                                <li><i className="ion-android-arrow-forward"></i> Базовое понимание реляционных баз данных</li>
                                <li><i className="ion-android-arrow-forward"></i> Будет плюсом: HTML, CSS, JS, Linq, веб-серсисы</li>
                            </ul>


                            <div className="tab-content">
                                <div id="curriculum" className="tab-pane fade in active">
                                    <p className="subheading"><span>План</span> курса</p>
                                    <table className="table table-striped">
                                        <tbody>
                                        { course.plan.map( item => {
                                            return (
                                                <tr key={item.id}>
                                                    <td><i className="ion-android-alarm-clock"></i></td>
                                                    <td>
                                                        <p className="lecture__title">{item.description}</p>
                                                    </td>
                                                    <td style={{width: '120px'}}>
                                                        <div className="lecture__time">{item.week}я неделя</div>
                                                    </td>
                                                </tr>
                                            )
                                        }) }
                                        </tbody>
                                    </table>
                                </div>
                                <div id="menu2" className="tab-pane fade">
                                    <h3>Menu 2</h3>
                                    <p>Sed ut perspiciatis unde omnis iste natus error sit voluptatem accusantium doloremque laudantium, totam rem aperiam.</p>
                                </div>
                                <div id="menu3" className="tab-pane fade">
                                    <h3>Menu 3</h3>
                                    <p>Eaque ipsa quae ab illo inventore veritatis et quasi architecto beatae vitae dicta sunt explicabo.</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
        </div>
    );
};

export default CoursePage;