import React from "react";
import './stats.css';
import stats from './store';

const StatsSection = () => {
    return (
        <section className="section__stats">
            <div className="container">
                <div className="row">
                    { stats.map( item => {
                        return (
                            <div key={item.id} className="col-sm-6 col-md-4">
                                <div className="stat__item">
                                    <div className="stats_item__icon">
                                        <i className={item.iconClass} aria-hidden="true"></i>
                                    </div>
                                    <div className="stats_item__nbr" data-from="0" data-to={item.count} data-speed="1000"
                                         data-refresh-interval="1">
                                        0
                                    </div>
                                    <div className="stats_item__text">{item.name}</div>
                                </div>
                            </div>
                        )
                    }) }
                </div>
            </div>
        </section>
    );
};

export default StatsSection;